@import url(https://fonts.googleapis.com/css2?family=Charis+SIL&family=Poppins:wght@300;400;500;600&family=Source+Sans+Pro&display=swap);
header{
    height: 100vh;
    overflow:hidden;
}

.header_container{
    text-align: center;
    height: 100%;

    position: relative;
}

/* *************** CTA **************** */
.cta{
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    padding-bottom: 2rem;
    justify-content: center;
}

.header_socials{
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;

}

.header_socials::before{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);

}

.me{
    border-radius: 1.6rem;
    overflow: hidden;
    margin-left: auto;
    margin-right: auto;
    aspect-ratio: .1/1;

     width: 39%;
}
.scroll_down{
    position:absolute;
    right:-2.3rem;
    bottom:5rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight:300;
    font-size: 0.9rem;
}

@media screen and (max-width: 1024px){
    header{
        height: 68vh;
    }
}

@media screen and (max-width: 600px){
    header{
        height: 100vh;
    }
    .me{
        width:82%;
    }
    .header_socials,
    .scroll_down{
        display: none;
    }
}
nav{
    background:  rgba(0, 0, 0, 0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding:  0.7rem 1.7rem;
    z-index: 2;
    position:fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(8px);
            backdrop-filter: blur(8px);
}
nav a{
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-white);
    font-size: 1.1rem;
}

nav a:hover{
    background: rgba(0, 0, 0, 0.3);
}

nav a.active{
    background: var(--color-bg);
    color: var(--color-white);

}
.about_container{
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
}
.about_me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        45deg,
        transparent,
        var(--color-primary),
        transparent
    );
    display: grid;
    place-items: center;
}
.about_me-image{
    border-radius:2rem;
    overflow:hidden;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    transition: var(--transition);
}
.about_me-image:hover{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}

.about_cards{
    display:grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap:1.5rem;
    gap:1.5rem;
}
.about_card{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding:2rem;
    text-align:center;
    transition:var(--transition);


}

.about_card:hover{
    background:transparent;
    border-color: var(--color-primary-variant);
    cursor:crosshair
    
}

.about_icon{
    color: var(--color-primary);
    font-size: 1.4rem;
}
.about_card h5{
    font-size: .95rem;
}
.about_card small{
    font-size: 0.7rem;
    color:var(--color-light);
}
.about_content p{
    margin: 2rem 0 2.6rem;
    font-size: 1.2rem;
    font-weight: 500;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: var(--color-light)
}
@media screen and (max-width: 1024px){
    .about_container{
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
    }
    .about_me{
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about_content p{
        margin: 1rem 0 1.5rem;
    }
}

@media screen and (max-width: 600px){
    .about_me{
        width: 65%;
        margin: 0 auto 3rem;
        position: center;
    }
    .about_cards{
        grid-template-columns: 1fr 1fr;
        grid-gap:1rem;
        gap:1rem;
        position: center;
    }
    .about_content{
        text-align: center;
    }
    .about_content p{
        margin: 1.5rem 0;
        position: center;
    }
}
.experience_container{
    display: grid;
    grid-gap: 1.5rem;
    gap: 1.5rem;
    grid-template-columns: repeat(2,1fr);
}

.experience_container>div{
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    width: 86%;
    margin-left: auto;
    margin-right: auto;

    border: 1px solid transparent;
    transition: var(--transition);
}

.experience_container>div:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor:default;
}
.experience_container > div h3{
    text-align: center;
    margin-bottom: 2rem;
    color:var(--color-primary);
}
.experience_content{
    display: grid;
}

.experience_details{
    display: grid;
    grid-template-columns: 1fr 1fr;

}
.experience_details-icon{
    font-size: 2rem;
    margin-left: 4px;
    color: var(--color-primary);

}
.detail{
    margin-top:3px;
    font-size: 2.5rem;
    color: var(--color-primary);
}

  
  
@media screen and (max-width: 1024px){
    .experience_container{
        grid-template-columns: 1fr;
    }
    .experience_container >div{
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }
    .experience_content{
        padding: 1rem;
    }
}

@media screen and (max-width: 600px){
    .experience_container{
        position: center;
        grid-gap: 1rem;
        gap: 1rem;
    }
    .experience_container > div{
        width:50%;
        position: right;
        padding: 2rem 1rem;
    }
}
.portfolio_container{
    display: grid;
    grid-template-columns: repeat(3 ,1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
}
.portfolio_item{
    background-color: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}
.portfolio_item:hover{
    border-color: var(--color-primary-variant);
    background: transparent;
}
.portfolio_item-image{
    border-radius: 1.5rem;
    overflow: hidden;

}
.portfolio_item h3{
    margin: 1.2rem 0 2rem;
}
.portfolio_item-cta{
    display: flex;
    margin-top: 1rem;
    display: inline;
    grid-gap:1rem;
    gap:1rem;
}
@media screen and (max-width: 1024px){
    .portfolio_container{
        grid-template-columns: 1fr 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }
}
@media screen and (max-width: 600px){
    .portfolio_container{
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
}
.container.contact_container{
    width: 58%;
    display: grid;
    grid-template-columns:30% 58%;
    grid-gap:12%;
    gap:12%;

}
.contact_options{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem
}

.contact_option{
    background:var(--color-bg-variant);
    padding: 1.2rem;
    text-align: center;
    border-radius: 2.6rem;
    border: 1px solid transparent;
    transition:var(--transition);
    display:list-item;
}
.contact_option:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
}
.contact-option-icon{
    font-size: 1.2rem;
    margin-bottom: 0.5rem;
}
.contact_option a{
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.9rem;
}
.contact_option h5{
    color: var(--color-light);
    font-size: 1.25rem;
}

/*FORM STLYING*/
form{
    display: flex;
    flex-direction: column;
    grid-gap:1.2rem;
    gap:1.2rem;
}
input,textarea{
    width:100%;

    padding:1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid gray;
    resize: none;
    color: white;
}

@media screen and (max-width: 1024px){
    .container.contact_container{
        grid-template-columns: 1fr ;
        grid-gap: 2rem;
        gap: 2rem;
    }
}
@media screen and (max-width: 600px){
    .container.contact_container{
        width:var(--container-width-sm);
        position: center;
        
    }
}


footer{
    background: black;
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}
footer a{
    color: rgb(116, 116, 157);
}
.footer_logo{
    font-size:2rem;
    font-weight: 500;
    margin-bottom:3rem;
    display:inline-block;
}
.footer_socials{
    display: flex;
    justify-content: center;
    grid-gap:1rem;
    gap:1rem;
    margin-bottom: 4rem;
}
.footer_socials a{
    background: var(--color-bg);
    color: rgb(116, 116, 157);
    padding:0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}
.footer_socials a:hover{
    background: transparent;
    color: var(--color-bg);
    border-color:var(--color-bg);
}
.footer_copyright{
    margin-bottom: 4rem;
    color: rgb(116, 116, 157);
}

.permalinks{
    display:flex;
    flex-wrap:wrap;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin-bottom: 1rem;
    margin: 0 auto 3 rem;
}
*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;

}
:root{
    --color-bg: #1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant: #rgba(77,181,255,.4);
    --color-white: #fff;
    --color-light:gray ;

    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
    
}
html{
    scroll-behavior: smooth;
}
::-webkit-scrollbar{
    display: none;
}
body{
    font-family: 'Georgia', sans-serif;    background: #1f1f38;    background: var(--color-bg);
    color: #fff;
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(/Portfolio/static/media/bg-texture.d5655c38.jpg);
}

/* *************************General Styles ************************** */
.container{
width: 75%;
width: var(--container-width-lg);
margin:0 auto;
};

h1,h2,h3,h4,h5{
    font-weight: 500;
}
h1{
    font-size: 2.5rem;
}
h2{
    font-size: 5rem;
}
h3{
    font-size: 2.5rem;
}
h5{
    font-size: 1.5rem;
}
h8{
    font-size: 2rem;
}
section{
    margin-top: 8rem;
}
section > h2, section > h5{
    text-align: center;
    color: gray;
    color: var(--color-light);
}
h6{
    color:lightblue;
    text-align: center;
    font-size: 3rem;
    font-style:oblique;
}
section > h2{
    color: #4db5ff;
    color: var(--color-primary);
    margin-bottom: 3rem;

}
.text-light{
    color: gray;
    color: var(--color-light);
}
a{
    color:#4db5ff;
    color:var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}
a:hover{
    color: #fff;
    color: var(--color-white);
}

.btn{
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #4db5ff;
    color: var(--color-primary);
    padding: .75rem 1.2rem;
    border-radius: 0.4rem;
    cursor:pointer;
    border: 1px solid #4db5ff;
    border: 1px solid var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}
.btn:hover{
    background: #fff;
    background: var(--color-white);
    color: #1f1f38;
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary{
    background: #4db5ff;
    background: var(--color-primary);
    color: #1f1f38;
    color: var(--color-bg);

}

img{
    display: block;
    width: 100%;
    object-fit: cover;
}

/******************** Media Queries (Medium Devices) ******************* */
@media screen and (max-width: 1024px){
    .container{
        width: 86%;
        width: var(--container-width-md);
    }
    section{
        margin-top: 6rem;
    }
}

/******************** Media Queries (Small Devices) ******************* */
@media screen and (max-width: 600px){
    .container{
        width: 90%;
        width: var(--container-width-sm);
    }
    section > h2{
        margin-top: 2rem;
    }
}


